/* Mobile first design, Portrait displays under 48em/768px */
.formStyle {
  display: block;
  width: 85%;
  margin: 0 auto;
}
#loginPage{
  background-image: url(/static/media/cheetah.2e81c759.jpg);
}

/* Chrome iPad present/Portrait 48em = 768px */
@media (min-width: 48em) {
  .formStyle {
    display: block;
    width: 50%;
    margin: 0 auto;
  }
}

/* Chrome iPad Pro Preset/Portrait 64em = 1024px */
@media (min-width: 64em) {
  .formStyle {
    display: block;
    width: 80%;
    margin: 0 auto;
  }
}

/* HD displays/Landscape 75em = 1200px */
@media (min-width: 75em) {
  .formStyle {
    display: block;
    width: 60%;
    margin: 0 auto;
  }
}

/* Full HD displays/Landscape 120em = 1920px */
@media (min-width: 120em) {
  .formStyle {
    display: block;
    width: 50%;
    margin: 0 auto;
  }
}

*{
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;  
}
#navbody{
 background: linear-gradient(60deg, black 40%, rgb(19, 43, 1));
  opacity: 0.8;


}
nav{  
  background: #171c24;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  height: 120px;
  padding: 0 100px;
  opacity: 0.5;
  filter: alpha(opacity=50); 
}
nav .logo{
  color: #fff;
  font-size: 30px;
  font-weight: 600;
  letter-spacing: -1px;
}
nav .nav-items{
  display: flex;
  flex: 1 1;
  padding: 0 0 0 40px;
}
nav .nav-items li{
  list-style: none;
  padding: 0 15px;
}
nav .nav-items li a{
  color: #fff;
  font-size: 24px;
  font-weight: 500;
  text-decoration: none;
}
nav .nav-items li a:hover{
  color: #00ff73;
}
nav form{
  display: flex;
  height: 40px;
  padding: 2px;
  background: #1e232b;
  min-width: 18%!important;
  border-radius: 2px;
  border: 1px solid rgba(155,155,155,0.2);
}

nav form button{
  padding: 0 15px;
  color: #fff;
  font-size: 20px;
  background: #00ff00;
  border: none;
  border-radius: 2px;
  cursor: pointer;
}
nav form button:hover{
  background: #00e673;
}
nav .menu-icon,
nav .cancel-icon,
nav .search-icon{
  width: 30px;
  text-align: center;
  margin: 0 50px;
  font-size: 18px;
  color: #fff;
  cursor: pointer;
  display: none;
}
nav .menu-icon span,
nav .cancel-icon,
nav .search-icon{
  display: none;
}
@media (max-width: 1245px) {
  nav{
    padding: 0 50px;
  }
}
@media (max-width: 1140px){
  nav{
    padding: 0px;
  }
  nav .logo{
    flex: 2 1;
    text-align: center;
  }
  nav .nav-items{
    position: fixed;
    z-index: 99;
    top: 70px;
    width: 100%;
    left: -100%;
    height: 100%;
    padding: 10px 50px 0 50px;
    text-align: center;
    background: #14181f;
    display: inline-block;
    transition: left 0.3s ease;
  }
  nav .nav-items.active{
    left: 0px;
  }
  nav .nav-items li{
    line-height: 40px;
    margin: 30px 0;
  }
  nav .nav-items li a{
    font-size: 20px;
  }
  nav form{
    position: absolute;
    top: 80px;
    right: 50px;
    opacity: 0;
    pointer-events: none;
    transition: top 0.3s ease, opacity 0.1s ease;
  }
  nav form.active{
    top: 95px;
    opacity: 1;
    pointer-events: auto;
  }
  nav form:before{
    position: absolute;
    content: "";
    top: -13px;
    right: 0px;
    width: 0;
    height: 0;
    z-index: -1;
    border: 10px solid transparent;
    border-bottom-color: #1e232b;
    margin: -20px 0 0;
  }
  nav form:after{
    position: absolute;
    content: '';
    height: 60px;
    padding: 2px;
    background: #1e232b;
    border-radius: 2px;
    min-width: calc(100% + 20px);
    z-index: -2;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
  nav .menu-icon{
    display: block;
  }
  nav .search-icon,
  nav .menu-icon span{
    display: block;
  }
  nav .menu-icon span.hide,
  nav .search-icon.hide{
    display: none;
  }
  nav .cancel-icon.show{
    display: block;
  }
}


.space{
  margin: 10px 0;
}
nav .logo.space{
  color: rgb(0, 255, 115);
  padding: 0 5px 0 0;
}
@media (max-width: 500px){
  nav .menu-icon,
  nav .cancel-icon,
  nav .search-icon{
    margin: 0 10px;
  }
  nav form{
    right: 30px;
  }
}
@media (max-width: 350px){
