    align-items: center;
    cursor: pointer;
  }
  
  .btn-slide img {
    width: 25px;
    height: 25px;
    pointer-events: none;
  }
  .prev {
    top: 50%;
    left: 20px;
    -webkit-transform: translateY(-60%);
            transform: translateY(-60%);
    opacity: 50%;
  }
  .next {
    top: 50%;
    right: 20px;
    -webkit-transform: translateY(-60%);
            transform: translateY(-60%);
    opacity: 50%;

